<template>
  <v-app id="app" dark>
    <v-toolbar class="toolbar">
      <v-toolbar-title class="white--text">VueCroppie</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon light>
        <!--<v-icon>more_vert</v-icon>-->
      </v-btn>
    </v-toolbar>
    <a href="https://github.com/jofftiquez/vue-croppie">
      <img style="position: absolute; z-index:999999; top: 50; right: 0; border: 0;" src="https://camo.githubusercontent.com/a6677b08c955af8400f44c6298f40e7d19cc5b2d/68747470733a2f2f73332e616d617a6f6e6177732e636f6d2f6769746875622f726962626f6e732f666f726b6d655f72696768745f677261795f3664366436642e706e67" alt="Fork me on GitHub" data-canonical-src="https://s3.amazonaws.com/github/ribbons/forkme_right_gray_6d6d6d.png">
    </a>
    <main>
      <v-container>
        <v-layout column class="mt-1 mb-4">
          <v-flex xs12>
            <v-card>
              <v-card-row>
                <v-card-text>
                  <h4 class="white--text">VueCroppie</h4>
                  <p>
                    <strong>VueCroppie</strong> is a
                    <a target="_blank" href="https://vuejs.org/">Vue 2</a> wrapper for
                    <a target="_blank" href="https://foliotek.github.io/Croppie/">Croppie</a> a beautiful photo cropping tool for Javascript by
                    <a target="_blank" href="http://www.foliotek.com/">foliotek</a>.
                  </p>
                  <h4 class="white--text">Installation</h4>
                  <h5 class="white--text">NPM</h5>
                  <code>npm install vue-croppie</code>
                  <h5 class="white--text">Usage</h5>
                  <code>import Vue from 'vue'</code>
                  <br>
                  <code>import VueCroppie from 'vue-croppie'</code>
                  <br>
                  <code>Vue.use(VueCroppie)</code>
                  <h5 class="white--text">Docs</h5>
                  <p>
                    See docs on <a target="_blank" href="https://github.com/jofftiquez/vue-croppie">GitHub</a>.
                  </p>
                </v-card-text>
              </v-card-row>
            </v-card>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs12 md6>
            <v-card>
              <v-card-row>
                <v-card-text>
                  <h4 class="white--text">Sample</h4>
                  <vue-croppie 
                    ref=croppieRef 
                    :enableOrientation="true"
                  ></vue-croppie>
                </v-card-text>
              </v-card-row>
              <v-card-row actions>
                <v-card-text>
                  <v-layout column align-center>
                    <v-flex p>
                      <v-btn primary light @click.native="bind()" v-tooltip:top="{ html: 'Bind a photo random photo url of my cats' }">Bind</v-btn>
                      <v-btn primary large icon @click.native="rotate(-90)" v-tooltip:top="{ html: 'Rotate Left -90' }">
                        <v-icon class="white--text">rotate_left</v-icon>
                      </v-btn>
                      <v-btn primary large icon @click.native="rotate(90)" v-tooltip:top="{ html: 'Rotate Right 90' }">
                        <v-icon class="white--text">rotate_right</v-icon>
                      </v-btn>
                      <v-btn primary large icon @click.native="crop" v-tooltip:top="{ html: 'Crop' }">
                        <v-icon class="white--text">crop</v-icon>
                      </v-btn>
                      <!--<v-btn @click.native="get()">Get</v-btn>-->
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card-row>
            </v-card>
          </v-flex>
          <v-flex xs12 md6>
            <v-card>
              <v-card-row>
                <v-card-text>
                  <h4 class="white--text">Result</h4>
                  <v-layout column align-center>
                    <v-flex p>
                      <img width="300" :src="cropped">
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card-row>
            </v-card>
          </v-flex>
        </v-layout>
  
      </v-container>
    </main>
    <v-footer class="pa-3">
      <v-spacer></v-spacer>
      <div> Jofferson Ramirez Tiquez {{ new Date().getFullYear() }}</div>
    </v-footer>
  </v-app>
</template>

<script>

export default {
  name: 'app',
  mounted() {
    this.$refs.croppieRef.bind({
      url: 'http://i.imgur.com/Fq2DMeH.jpg',
    })
    setTimeout(() => {
      this.$refs.croppieRef.result((output) => {
        this.cropped = output;
      })
    }, 500)
  },
  data() {
    return {
      cropped: null,
      images: [
        'http://i.imgur.com/fHNtPXX.jpg',
        'http://i.imgur.com/ecMUngU.jpg',
        'http://i.imgur.com/7oO6zrh.jpg',
        'http://i.imgur.com/miVkBH2.jpg'
      ]
    }
  },
  methods: {
    bind() {
      let url = this.images[Math.floor(Math.random() * 4)]
      this.$refs.croppieRef.bind({
        url: url,
      })
    },
    get() {
      this.$refs.croppieRef.get((points) => {
        console.log(points);
      });
    },
    crop() {
      this.$refs.croppieRef.result((output) => {
        this.cropped = output;
      });
    },
    rotate(rotationAngle) {
      this.$refs.croppieRef.rotate(rotationAngle);
    }
  }
}
</script>

<style stylus>
#app {
  font-family: 'Roboto', sans-serif;
  /*background: #f9f9f9;*/
}

body {
  font-family: 'Roboto', sans-serif;
  background: #f9f9f9;
}

a {
  color: white;
  text-decoration: underline;
  font-weight: bold;
}

.cropper {}

.result {}
</style>



